/**
 * 
 * @param fn 
 * @param timeout 
 * @return 
 * @example document.onscroll = defer(() => console.log(""), 100);
 */
export function defer(fn, duration = 0) {
    let timer;
    return function () {
        const args = arguments;
        timer && clearTimeout(timer);
        timer = setTimeout(() => {
            timer = 0;
            fn.apply(this, args);
        }, duration);
    };
}
