import * as React from 'react';
/**
 * 
 * @param {any} Node &&
 * */
export function getReactNode(Node) {
    return (React.isValidElement(Node) || typeof Node === 'string') ? Node : <Node />;
}
/**
 *  css3 transform 
 * @param {element} ele 
 * @param {string} attribute 
 * */
export function setTransform(ele, attribute) {
    const transformNames = {
        webkit: 'webkitTransform',
        Moz: 'MozTransform',
        O: 'OTransform',
        ms: 'msTransform',
        standard: 'transform'
    };
    for (let name in transformNames) {
        if (ele.style[transformNames[name]] !== undefined) {
            ele.style[transformNames[name]] = attribute;
        }
    }
}
/**
 * metacontent
 * @param {string} name metaname
 * */
export function getMetaValue(name) {
    const meta = Array.from(document.getElementsByTagName('meta'))
        .filter(item => item.name === name)[0];
    let value = meta ? meta.content && meta.content.toLowerCase() : null;
    if (name === 'theme' && value !== 'ios' && value !== 'android')
        value = 'ios';
    return value;
}
/**
 * 
 * */
export function getUuidFn(key) {
    let id = 0;
    const nowDate = Date.now();
    return () => `${key}_${nowDate}_${id++}`;
}
